body {
  cursor: help;
  color: #4a4a4a;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  padding: 0;
  max-width: 600px;
  margin: auto;
}

p, li {
  line-height: 1.5em;
  font-size: 18px;
}

h3 {
  padding-bottom: 20px;
  font-size: 20px;
  text-align: left; }

.selector::after{
  content: '\A';
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

ol {
  padding: 0;
}

ol li {
  height: auto;
}



