.toggle {
  cursor: pointer;
  z-index: 100;
}

.float-icon {
  top: 10vh;
  padding: 40;
  position: absolute;
  right: 20vw;
}

@media screen and (max-height: 320px), screen and (max-width: 320px) {
  .ThumbPrint {
    bottom: 18px;
    right: 18px;
    height: 39px;
    width: 36px;
  }

  .x {
    top: 18px;
    left: 18px;
    height: 39px;
    width: 36px;
  }

  .textbox, li {
    font-size: 12px;
  }

  .Modal {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 20px;
  }

  .float-icon {
    top: auto;
    bottom: 5vh;
    padding: 40;
    position: absolute;
    right: 5vw;
  }
}



